import React, {Component} from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import classnames from 'classnames';
import './Navbar.css';

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {active: false}
  }

  displayNav = () => {
    this.setState({active: !this.state.active});
  }

  render() {
    let navDisplay = classnames({'nav-display': this.state.active});

    return (
      <nav>
        <div className={`nav-links ${navDisplay}`}>
          <span>
            <AnchorLink href='#home' onClick={this.displayNav}>Home</AnchorLink>
          </span>
          <span>
            <AnchorLink href='#about' onClick={this.displayNav}>About</AnchorLink>
          </span>
          <span>
            <AnchorLink href='#resume' onClick={this.displayNav}>Resume</AnchorLink>
          </span>
          <span>
            <AnchorLink href='#portfolio' onClick={this.displayNav}>portfolio</AnchorLink>
          </span>
          <span>
            <AnchorLink href='#contact' onClick={this.displayNav}>Contact</AnchorLink>
          </span>
        </div>
        <span className='nav-button' onClick={this.displayNav}>
          <i className="fas fa-bars fa-2x"></i>
        </span>
      </nav>
    );
  }
}

export default Navbar;