import React from 'react';
import Fade from 'react-reveal/Fade';
import './Contact.css';

const Contact = () => {
  return (
    <div id='contact' className='section'>
      <Fade left>
        <h1>Contact</h1>
        <div className='title-line'></div>
      </Fade>
      <Fade up>
        <div className='contact-info'>
          <div>
            <a href='mailto:jake@example.com' target='_blank' rel='noopener noreferrer'>
              <i class="far fa-envelope fa-2x"></i>
            </a>
          </div>
          <div>
            <a href='tel:+15555555555'>
              <i class="fas fa-phone fa-2x"></i>
            </a>
          </div>
          <div>
            <a href='https://github.com/jburto94' target='_blank' rel='noopener noreferrer'>
              <i class="fab fa-github fa-2x"></i>
            </a>
          </div>
          <div>
            <a href='https://codepen.io/jburton94/' target='_blank' rel='noopener noreferrer'>
              <i class="fab fa-codepen fa-2x"></i>
            </a>
          </div>
        </div>
      </Fade>
    </div>
  )
}

export default Contact;