import React from 'react';
import Fade from 'react-reveal/Fade';
import BarDisplay from './BarDisplay';
import './About.css';

const About = () => {
  return (
    <div id='about' className='section'>
      <Fade left>
        <h1>About Me</h1>
        <div className='title-line'></div>
      </Fade>
      <div className='about-info'>
        <Fade up>
          <div className='personal'>
            <img src='./images/profile-pic.png' alt='Jake Burton'></img>
            <h3>Who am I?</h3>
            <p>I am a web developer in Greenville, SC. Hoping to improve my skills and gain more professional experience. Always looking to learn
            and challenge myself with new technologies.</p>
          </div>
        </Fade>
        <BarDisplay />
      </div>
    </div>
  )
}

export default About;