import React, {Component} from 'react';
import Modal from 'react-responsive-modal';
import './Project.css';

class Project extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false
    };
  }

  onOpenModal = () => {
    this.setState({ open: true });
  };
 
  onCloseModal = () => {
    this.setState({ open: false });
  };

  render() {
    const {name, tech, pic, pos, about, link, github} = this.props;
    const {open} = this.state;
    const style = {
      background: `url(${pic}) no-repeat ${pos}% 0%`,
      backgroundSize: 'cover'
    };

    return (
      <div className='project' style={style}>
        <div className='project-cover hover-trans'>
          <div className='project-info'>
            <h3>{name}</h3>
            <h5>{tech}</h5>
          </div>
          <button className='learn-btn hover-trans' onClick={this.onOpenModal}>Learn More</button>
          <Modal 
            open={open} 
            onClose={this.onCloseModal}
            center
          >
            <div className='modal'>
              <img src={pic} alt={name} />
              <h3>{name}</h3>
              <p className='about-project'>{about}</p>
              <div className='project-links'>
                <a href={github} target='_blank' rel='noopener noreferrer' className='hover-trans'>
                  <span>See the code <i className="fas fa-external-link-alt"></i></span>
                </a>
                <a href={link} target='_blank' rel='noopener noreferrer' className='hover-trans'>
                  <span>See it in action <i className="fas fa-external-link-alt"></i></span>
                </a>
              </div>
            </div>
          </Modal>
        </div>
      </div>
    );
  }
}

export default Project;