import React from 'react';
import {projectData} from './projectData';
import Project from './Project';
import './ProjectDisplay.css';

const ProjectDisplay = () => {
  const projects = projectData.map(project => {
    return (
      <div className='proj'>
        <Project 
          name={project.name} 
          tech={project.tech} 
          pic={project.pic} 
          pos={project.pos} 
          about={project.about} 
          link={project.link}
          github={project.github}
        />
        <div className='project-on-mobile'>
          <h3>{project.name}</h3>
          <p className='about-project'>{project.about}</p>
          <div className='project-links'>
            <a href={project.github} target='_blank' rel='noopener noreferrer'>
              <span>See the code <i className="fas fa-external-link-alt"></i></span>
            </a>
            <a href={project.link} target='_blank' rel='noopener noreferrer' className='hover-trans'>
              <span>See it in action <i className="fas fa-external-link-alt"></i></span>
            </a>
          </div>
        </div>
      </div>
    );
  });

  return (
    <div className='projects'>
      {projects}
    </div>
  );
}

export default ProjectDisplay;